import React, { useLayoutEffect, useState } from 'react';
import PropTypes from 'prop-types';
import errorBoundary from '@src/components/errorBoundary';
import Comments from '@src/mobile/components/Comments';
// import { useWeixinLogin } from '@src/public/hooks/useWeixinLogin';
import { getQuery } from '@src/public/utils/url';
import Event404 from '@src/mobile/components/Page404';
import styles from './index.css';
import Header from './Header';

const Layout = ({ content }) => {
    const { event, params } = content;
    const { subjectId } = params;
    const allowComment = event.allowComment === '1';

    // useWeixinLogin();

    const [initDone, setInitDone] = useState(false);
    const [replyId, setReplyId] = useState('');

    useLayoutEffect(() => {
        const msgExtraId = getQuery(window.location.href, 'msgExtraId');

        if (msgExtraId) {
            setReplyId(msgExtraId);
        }

        setInitDone(true);
    }, []);

    return (
        <>
            {event ? (
                <div className={styles.layout}>
                    <Header content={event} />
                    {initDone ? <Comments subjectId={subjectId} replyId={replyId} allowComment={allowComment} /> : null}
                </div>
            ) : (
                <Event404 />
            )}
        </>
    );
};

Layout.propTypes = {
    content: PropTypes.object,
};

export default errorBoundary(Layout);
