import React from 'react';
import PropTypes from 'prop-types';
import errorBoundary from '@src/components/errorBoundary';
import { useMaximize } from '@src/public/hooks/app';
import { groundsClose, isGrounds } from '@src/public/utils/app';
import styles from './index.css';
import Link, { linkBase } from '@src/mobile/components/Link';

const Header = ({ content }) => {
    const statusBarHeight = useMaximize();

    return (
        <div className={styles.wrap} style={{ paddingTop: statusBarHeight }}>
            {isGrounds ? (
                <div className={styles.content}>
                    <div
                        className={styles.back}
                        onClick={() => {
                            groundsClose();
                        }}
                    />
                    <div className={styles.title}>热门评论</div>
                    <div className={styles.more} />
                </div>
            ) : null}
            <Link to={
            process.env.NODE_ENV === 'development' ?
            `${linkBase}/loc/timeline/event/${content.base62Id}` :
                `${window.location.origin}/timeline/mobile_event.html`
                
                } className={styles.eventLink}>
                <div className={styles.event}>
                    <div className={styles.eventText}>{content.title}</div>
                    <div className={styles.eventArrow} />
                </div>
            </Link>
        </div>
    );
};

Header.propTypes = {
    content: PropTypes.object,
};

export default errorBoundary(Header);
